import React from 'react';
import { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import Collapse from 'react-bootstrap/Collapse'


export default function Accomodation() {
  const [open, setOpen] = useState(false);

  return (
    <div>
      
          <li id="accomodation"onClick={() => setOpen(!open)}
        aria-controls="example-collapse-text"
                      aria-expanded={open}>ACCOMODATION</li>
                   <Collapse in={open}>
           <div id="example-collapse-text"  className="modal1">
       
                  <p>In Sicily we were accomodate in
                 Marku's House. </p>
                    <p>
                        Accomodation was very average, but count on that how close was to downtown, it worth for price.
                    </p>
                
                  </div>
                
   </Collapse>     
         
          </div>
  );
}