import React from 'react';
import { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import Collapse from 'react-bootstrap/Collapse'


export default function Places() {
  const [open, setOpen] = useState(false);

  return (
    <div>
      
      <li id="Places" onClick={() => setOpen(!open)}
        aria-controls="example-collapse-text"
              aria-expanded={open}>PLACES TO VISIT</li>
          <Collapse in={open} className="modal3">
          <div id="example-collapse-text" >
          
          <p> I think that ther are endless places in Sicily, that you can wisit. All you need is avanturistic
                    spirit,<br></br>
                    good energy and daily bas ticket.
                </p>
                <p>We have managed to visit:</p>
                <ul class="places">
                    <li>Teatro Masimo</li>
                    <li>Palermo catedral</li>
                    <li>Mondelo Beach</li>
                    <li>San Vito locopo</li>
                    <li>Ursino castle</li>
                    <li>Taormina</li>
                </ul>
                  </div>
                  
          </Collapse>
         
          </div>
  );
}