import React from 'react';
import { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import Collapse from 'react-bootstrap/Collapse'


export default function Transport() {
  const [open, setOpen] = useState(false);

  return (
    <div>
      
      <li id="Transport" onClick={() => setOpen(!open)}
        aria-controls="example-collapse-text"
              aria-expanded={open}>TRANSPORT</li>
          <Collapse in={open}>
              
              <div id="example-collapse-text" className="modal2" >
              <p>For traveling to beach we used local bus companies in Palermo and Catania.<br></br>
                    Transport was cheap and well organized. For far travelings you can use variety
                    of buss companies. You can rent a car. Price is 20 eur per day. Also you can
                    use train and enjoe in beautiful Sicily landscape.
                </p>
                  </div>
                
       </Collapse>
         
          </div>
  );
}