import React from 'react';
import { Link,useNavigate} from "react-router-dom";
import 'bootstrap/dist/css/bootstrap.css';

import Accomodation from "./Accomodatio_sicily"
import Transport from "./Transport_sicily"
import Places from "./visit_places_sicily"


export default function Information() {
  const home = process.env.PUBLIC_URL + "/"
  const navigate = useNavigate()
  const handleClick = (e) => {
    e.preventDefault()
    navigate(home)
  }
  return (
    <>
      <nav className="content01">
      <ul>
          <Accomodation open={true}/>
          <Transport/>
          <Places/>
      </ul>
      <button id="button"><Link to="/" onClick={handleClick}>BACK TO ROME</Link></button>
  </nav>
      
          
          
          </>
  );
}