import React from 'react';
import { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import Collapse from 'react-bootstrap/Collapse'


export default function Transport() {
  const [open, setOpen] = useState(false);

  return (
    <div>
      
      <li id="Transport" onClick={() => setOpen(!open)}
        aria-controls="example-collapse-text"
              aria-expanded={open}>TRANSPORT</li>
          <Collapse in={open}>
              
              <div id="example-collapse-text" className="modal2" >
           <p>For traveling to Rome we use bus from airport. The company name was
               <a href="https://www.terravision.eu">Terravision</a>.
           </p>
           <p>The bus was right on time and price was 6 EUR in one way</p>
           <p>
               For local purposes, in city of Rom, we use daily card, which cover transport with bus, tramp and
               metro.<br></br>
               Price was 7 EUR for 24 hours. Good investment if you want to see the most monuments in city.
           </p>
                  </div>
                
       </Collapse>
         
          </div>
  );
}